* {
    box-sizing: border-box;
    margin: 0;
}
.body {
    font-family: "TT Norms", "Arial", sans-serif;
    font-style: normal;
    font-weight: 400;
    overflow-x: hidden;
}
.visually-hidden {
    position: absolute;
    width: 1px;
    height: 1px;
    margin: -1px;
    border: 0;
    padding: 0;
    clip: rect(0 0 0 0);
    overflow: hidden;
}
.img-responsive {
    max-width: 100%;
    height: auto;
}
.container {
    max-width: 1320px;
    min-width: 375px;
    margin: 0 auto;
}
a {
    &:hover {
        text-decoration: none;
    }
}


